import { Link } from 'react-router-dom'

import './form.css'

const Checkbox = ({ isChecked, onChange, children }) => {
	return (
		<label className='checkboxWrp'>
			<input
				type='checkbox'
				className='checkbox-element'
				checked={isChecked}
				onChange={() => onChange(prev => !prev)}
				required
			/>
			<p>{children}</p>
		</label>
	)
}

